.CurInput {
    background-color: #335;
    width: 350px;
    height: 60px;
    margin: 10px auto 10px;
    display: grid;
    grid-template-columns: 250px 90px;
    border-radius: 15px;
    font-weight: bold;
}
input, select {
    background: none;
    border: 0;
    color: #fff;
    font-family: "Sofia Sans", sans-serif;
}
input {
    padding-left: 10px;
    border-radius: 15px;
}
select{
    padding: 15px;
    margin-left: 10px;
    background-color: #335;
    border-radius: 15px;
}
